/*** Parameters ***/

// font
$fonts: "LXGW WenKai Screen", -apple-system, Verdana, 'PingFang SC', 'Helvetica Neue', 'Arial', 'Hiragino Sans GB', 'Microsoft YaHei', 'WenQuanYi Micro Hei', sans-serif;

// color
$color-theme-default: rgb(27, 126, 255);
$color-theme-default-1: rgb(27, 126, 255, .8);
$color-theme-default-2: rgb(27, 126, 255, .6);
$color-theme-default-3: rgb(27, 126, 255, .3);
$color-theme-default-4: rgb(27, 126, 255, .1);
$color-theme-pink: rgb(252, 103, 148);
$color-theme-pink-1: rgb(252, 103, 148, .8);
$color-theme-pink-2: rgb(252, 103, 148, .6);
$color-theme-pink-3: rgb(252, 103, 148, .3);
$color-theme-pink-4: rgb(252, 103, 148, .1);
$color-bg: #F7F8FA;
$color-brand: #B8BDC3;
$color-dark-primary: #2d313a;
$color-dark-secondary: #2D313A;
$color-gray-border: #d6e3ed;
$color-gray-border-lighter: #5c93bb2b;

$color-info-dark: #009400;
$color-info-darker: #008b00;
$color-info-darkest: #007300;
$color-info-light: #26b226;
$color-info-lighter: #4dbf4d;
$color-info-lightest: #80d280;
$color-info-contrast-background: #e6f6e6;
$color-info-contrast-foreground: #003100;
$color-info-code-background: rgba(0,164,0,.15);
$color-note-dark: #4cb3d4;
$color-note-darker: #47a9c9;
$color-note-darkest: #3b8ba5;
$color-note-light: #6ecfef;
$color-note-lighter: #87d8f2;
$color-note-lightest: #aae3f6;
$color-note-contrast-background: #eef9fd;
$color-note-contrast-foreground: #193c47;
$color-note-code-background: rgba(84,199,236,.15);
$color-warning-dark: #e6a700;
$color-warning-darker: #d99e00;
$color-warning-darkest: #b38200;
$color-warning-light: #ffc426;
$color-warning-lighter: #ffcf4d;
$color-warning-lightest: #ffdd80;
$color-warning-contrast-background: #fff8e6;
$color-warning-contrast-foreground: #4d3800;
$color-warning-code-background: rgba(255,186,0,.15);
$color-error-dark: #e13238;
$color-error-darker: #d53035;
$color-error-darkest: #af272b;
$color-error-light: #fb565b;
$color-error-lighter: #fb7478;
$color-error-lightest: #fd9c9f;
$color-error-contrast-background: #fdf7f7;
$color-error-contrast-foreground: #4b1113;
$color-error-code-background: rgba(250,56,62,.15);
$color-citation-darkest: #495057;
$color-citation-contrast-background: #f8f9fa;
$color-citation-contrast-foreground: #323940;
$color-citation-code-background: #fdfdfe;

$color-post-copyright: #333;
$color-post-copyright-background: #FFFBF0;
$color-post-copyright-border: #EFE1BB;
$color-post-copyright-tips: #E32E00;

// font-color
$color-f-primary: #4E4E4E;
$color-f-secondary: #656565;
$color-f-dark: #AAAAAA;

// size
$g-header-h: 26px;
$g-banner-h: 460px;
$g-footer-h: 40px;
$g-container-w: 1080px;
$post-content-w: 695px;

.color-f-white {
    color: #fff;
}
.color-f-gray {
    color: #666!important;
}

/*** Mixins ***/
@mixin clearfix {
    &:before,
    &:after {
        content: '';
        display: table;
    }
    &:after {
        clear: both;
    }
}

@mixin tagStyle {
    display: inline-block;
    width: auto;
    height: 24px;
    line-height: 24px;
    padding: 0 12px;
    font-size: 14px;
    color: #656565;
    box-sizing: border-box;
    box-shadow: 0 0 .6px #D5DAE1;
    border: 1px solid $color-gray-border-lighter;
    border-radius: 12px;
    background-color: $color-bg;
    cursor: pointer;
    margin-right: 2px;
    transition: .2s;
    margin-bottom: 5px;
    &:last-child {
        margin-right: 0;
    }
}

@mixin default {
    &:hover {
        color: $color-theme-default!important;
    }
}

@mixin pink {
    &:hover {
        color: $color-theme-pink!important;
    }
}

@mixin cardStyle {
    padding: 20px 20px;
    background-color: #fff;
    border: 1px solid #E7EAF1;
    border-radius: 3px;
    box-sizing: border-box;
    box-shadow: 0 1px 3px rgba(0, 37, 55, .06);
}

@mixin authorCard {
    text-align: center;

    .avatar {
        width: 70px;
        height: 70px;
        border-radius: 50%;
        margin: 0 auto;
        overflow: hidden;
        box-shadow: 0 1px 4px rgba(100, 110, 120, .53);
        img {
            width: 70px;
            height: 70px;
        }
    }
    .author-name {
        width: 100%;
        font-size: 16px;
        color: $color-f-primary;
        font-weight: 500;
        margin: 12px 0 6px;
    }
    .bio {
        p {
            font-size: 14px;
            line-height: 20px;
        }
    }
    .level {
        display: flex;
        margin-top: 20px;
        .level-item {
            flex-grow: 1;
            .heading {
                display: block;
                font-size: 11px;
                letter-spacing: 1px;
                margin-bottom: 5px;
                text-transform: uppercase;
            }
            .title {
                color: #363636;
                font-size: 2rem;
                font-weight: 400;
                line-height: 1.125;
            }
        }
    }
    .sns-links {
        margin-top: 14px;
        cursor: default;
        li {
            position: relative;
            display: inline-block;
            width: 26px;
            height: 26px;
            overflow: hidden;
            a {
                display: inline-block;
            }
            .iconfont {
                font-size: 22px;
                line-height: 30px;
            }
            @include snsIcon;
        }
    }
}

@mixin timeStyle {
    color: #585858;
    display: inline-block;
    font-size: 14px;
    letter-spacing: .6px;
    line-height: 22px;
    min-width: 110px;
}

@mixin snsIcon {
    .iconfont {
        color: #B8BDC3;
        transition: .2s;
        &:hover {
            color: #7B848F;
        }
    }
    .icon {
        color: #B8BDC3;
        transition: .2s;
        width: 22px;
        height: 22px;
        line-height: 30px;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
        filter: grayscale(100%);
        -webkit-filter: grayscale(100%);
        -moz-filter: grayscale(100%);
        -ms-filter: grayscale(100%);
        &:hover {
            color: #7B848F;
            filter: none;
            -webkit-filter: none;
            -moz-filter: none;
            -ms-filter: none;
        }
    }
    .icon-weibo:hover {
        color: #F85555;
    }
    .icon-zhihu:hover {
        color: #1892F5;
    }
    .icon-twitter:hover {
        color: #39A6F8;
    }
    .icon-instagram:hover {
        color: #D92580;
    }
    .icon-juejin:hover {
        color: #1682FC;
    }
    .icon-douban:hover {
        color: #2E963D;
    }
    .icon-github:hover {
        color: #575757;
    }
    .icon-facebook:hover {
        color: #3D5A9A;
    }
    .icon-dribbble:hover {
        color: #F26798;
    }
    .icon-jianshu:hover {
        color: #EA6F5A;
    }
    .icon-uicn:hover {
        color: #3498DB;
    }
    .icon-linkedin:hover {
        color: #3181BE;
    }
    .icon-medium:hover {
        color: #0BE370;
    }
    .icon-youtube:hover {
        color: #FF2600;
    }
    .icon-behance:hover {
        color: #0057ff;
    }
    .icon-vk:hover {
        color: #5181b8;
    }

}

